<template>
  <div class="container">
    <div class="header">
      <div class="title">
        <span>{{ queryInfo.cat }}</span>
        <el-button
          type="primary"
          @click="dialogVisible = !dialogVisible"
          size="medium"
        >
          选择分类<i class="el-icon-arrow-down"></i>
        </el-button>
      </div>
      <div v-show="dialogVisible" class="catelist">
        <dl v-for="(item, index, i) in categories" :key="index">
          <dt>{{ i | format }}</dt>
          <dd>
            <button
              v-for="(subitem, index) in item"
              :key="index"
              @click="handleClick(subitem.name)"
              :class="{ active: subitem.name === queryInfo.cat }"
            >
              {{ subitem.name }}
            </button>
          </dd>
        </dl>
      </div>
    </div>
    <hr />
    <PlaylistsList :playlists="playlists"></PlaylistsList>
    <div class="footer">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="queryInfo.limit"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import PlaylistsList from "@/components/PlaylistsList.vue";
export default {
  components: {
    PlaylistsList,
  },
  filters: {
    format(val) {
      switch (val) {
        case 0:
          return "语种";
        case 1:
          return "风格";
        case 2:
          return "场景";
        case 3:
          return "情感";
        case 4:
          return "主题";
      }
    },
  },
  data() {
    return {
      categories: {
        language: [],
        style: [],
        scenes: [],
        emotion: [],
        theme: [],
      },
      queryInfo: {
        cat: "全部",
        limit: 30,
        offset: 0,
      },
      playlists: [],
      total: 0,
      dialogVisible: false,
      currentPage: 1,
    };
  },
  methods: {
    async getCategories() {
      const { data: res } = await this.$http.get("/playlist/catlist");
      // console.log(res);
      res.sub.forEach((item) => {
        switch (item.category) {
          case 0:
            this.categories.language.push(item);
            break;
          case 1:
            this.categories.style.push(item);
            break;
          case 2:
            this.categories.scenes.push(item);
            break;
          case 3:
            this.categories.emotion.push(item);
            break;
          case 4:
            this.categories.theme.push(item);
            break;
        }
      });
    },
    async getPlaylists() {
      const { data: res } = await this.$http.get("/top/playlist", {
        params: this.queryInfo,
      });
      // console.log(res);
      this.playlists = res.playlists;
      this.total = res.total;
    },
    handleClick(val) {
      this.queryInfo.cat = val;
      this.currentPage = 1;
      this.queryInfo.offset = 0;
      this.getPlaylists();
      this.dialogVisible = false;
    },
    handleCurrentChange() {
      this.queryInfo.offset = (this.currentPage - 1) * this.queryInfo.limit;
      this.getPlaylists();
    },
  },
  created() {
    this.getCategories();
    this.getPlaylists();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    padding: 20px;
    .title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        font-size: 25px;
      }
    }
    .catelist {
      padding-top: 20px;
      dl {
        display: flex;
        align-items: flex-start;
        dt {
          padding: 8px;
          font-size: 15px;
          color: #303133;
        }
        dd {
          flex: 1;
          display: flex;
          flex-wrap: wrap;
          button {
            padding: 8px 15px;
            border: none;
            outline: none;
            background-color: transparent;
            cursor: pointer;
            font-size: 15px;
            color: #606266;
            &:hover {
              color: #409eff;
            }
            &.active {
              background-color: #409eff;
              color: #ffffff;
            }
          }
        }
      }
    }
  }
  hr {
    border: 1px solid #303133;
    margin-bottom: 20px;
  }
  .footer {
    display: flex;
    justify-content: center;
  }
}
</style>